<template>
  <Header />
  <main class="m-auto h-auto mt-4 w-full">
    <template v-if="loading"><Loader /></template>
    <template v-else-if="error"
      ><Error v-bind:error="error.message" />
    </template>
    <template v-else-if="allData">
      <Main v-bind:allData="allData" />
    </template>
  </main>
  <Footer />
</template>

<script>
import { useQuery, useResult } from "@vue/apollo-composable";
import Header from "@/components/Header.vue";
import Footer from "@/components/Footer.vue";
import Main from "@/components/Main.vue";
import Error from "@/views/Error.vue";
import Loader from "@/views/Loader.vue";
import { allDataQuery } from "./queries/queries.js";

export default {
  name: `App`,
  components: {
    Header,
    Footer,
    Main,
    Error,
    Loader,
  },
  setup(props) {
    const { result, loading, error, refetch } = useQuery(
      allDataQuery,
      () => ({
        START_DATE: null,
      }),
      {
        fetchPolicy: "cache-first",
      }
    );
    const allData = useResult(result, null, (data) => data.allData);

    return { allData, loading, error };
  },
};
</script>
