<template>
  <li class="w-1/7 ">
    <span class="">
      <h2
        :title="titleDay"
        class="uppercase font-bold text-center bg-mainTableHeaderColor"
      >
        {{ entrie.day }}
      </h2>
      <p class="text-titleDayFont text-center bg-mainTableHeaderColor">
        {{ titleDay }}
      </p>
    </span>
    <DiaryItemData
      v-for="Entrie in entrie.data"
      v-bind:key="Entrie._id"
      v-bind:Entrie="Entrie"
    />
  </li>
</template>

<script>
import { ref, toRefs } from "vue";
import DiaryItemData from "./DiaryItemData.vue";
export default {
  components: { DiaryItemData },
  name: `DiaryItem`,
  props: {
    entrie: Object,
  },
  setup(props) {
    let titleDay = ref(
      new Date(parseInt(props.entrie.data[0].date)).toLocaleDateString(
        "ru-RU",
        {
          // weekday: "long",
          year: "numeric",
          month: "numeric",
          day: "numeric",
        }
      )
    );
    return { titleDay };
  },
};
</script>

