<template>
  <div class="form_radio_group mx-auto">
    <div class="form_radio_group-item">
      <input
        id="radio-1"
        type="radio"
        name="radio"
        value="86400000"
        @click="setGlucoseData"
      />
      <label for="radio-1">24h</label>
    </div>
    <div class="form_radio_group-item">
      <input
        id="radio-2"
        type="radio"
        name="radio"
        value="259200000"
        @click="setGlucoseData"
      />
      <label for="radio-2">3d</label>
    </div>
    <div class="form_radio_group-item">
      <input
        id="radio-3"
        type="radio"
        name="radio"
        value="604800000"
        @click="setGlucoseData"
      />
      <label for="radio-3">7d</label>
    </div>
    <div class="form_radio_group-item">
      <input
        id="radio-4"
        type="radio"
        name="radio"
        value="2629743000"
        @click="setGlucoseData"
      />
      <label for="radio-4">30d</label>
    </div>
  </div>
  <div
    class="
      glucose_data
      flex flex-wrap
      justify-between
      mt-2
      border-2
      rounded-lg
      p-2
      bg-mainTableColor
    "
  >
    <ul class="glucose_range w-auto flex flex-wrap justify-center">
      <li class="mr-2 text-red-600">
        <p>Высокий</p>
        <p>{{ hightGlucose }}%</p>
      </li>
      <li class="mr-2 text-green-600">
        <p>В норме</p>
        <p>{{ normGlucose }}%</p>
      </li>
      <li class="mr-2 text-blue-600">
        <p>Низкий</p>
        <p>{{ lowGlucose }}%</p>
      </li>
      <li class="mr-2">
        <p>Средний</p>
        <p>{{ averageGlucose }} mmol/l</p>
      </li>
    </ul>
    <div
      class="
        h-glucoseCircleHeight
        w-glucoseCircleWidth
        rounded-full
        m-5
        shadow-2xl
      "
      :style="{ background: conicGradientCalc }"
    ></div>
  </div>
</template>

<script>
import { inject, ref, computed } from "vue";
import { calcGlucoseData } from "../composables/glucoseLevel.js";
export default {
  name: "Timeline",
  setup(props) {
    const allData = inject("allData");

    let lowGlucose = ref(0);
    let hightGlucose = ref(0);
    let normGlucose = ref(0);
    let averageGlucose = ref(0);

    let setGlucoseData = (time) => {
      const { low, hight, norm, average } = calcGlucoseData(
        allData,
        parseInt(time.target.value)
      );
      lowGlucose.value = low;
      normGlucose.value = norm;
      hightGlucose.value = hight;
      averageGlucose.value = average;
    };
    const conicGradientCalc = computed(() => {
      return `conic-gradient(#2563eb 0,#2563eb ${lowGlucose.value}%,#dc2626 0,#dc2626 ${hightGlucose.value}%,#16a34a 0,#16a34a 100%)`;
    });
    return {
      lowGlucose,
      hightGlucose,
      normGlucose,
      averageGlucose,
      setGlucoseData,
      conicGradientCalc,
    };
  },
};
</script>

