<template>
  <router-view />
</template>

<script>
import { provide } from "vue";
import { genAllEntries, genWeekEntries } from "../composables/entries.js";
export default {
  name: `Main`,
  props: {
    allData: {
      type: Array,
    },
  },
  setup(props) {
    const { newAllEntries } = genAllEntries(props.allData);
    const { startPeriod, newEntries, endPeriod } = genWeekEntries(
      props.allData
    );
    provide("newAllEntries", newAllEntries);
    provide("allData", props.allData);
    provide("weekEntries", {
      startPeriod,
      newEntries,
      endPeriod,
    });

    return {};
  },
};
</script>

