import { ref } from "vue";
export function calcGlucoseData(allData, timeRange) {
  const data = allData.filter(
    (el) => parseInt(el.date) > parseInt(allData[0].date) - timeRange
  );

  let average = (
    data.reduce((total, next) => total + next.sgv / 18.02, 0) / data.length
  ).toFixed(1);

  let HIGHT = [];
  let LOW = [];
  let NORM = [];

  data.map((data) => {
    let sgv = (data.sgv / 18.02).toFixed(1);
    if (sgv >= 10) {
      HIGHT.push(sgv);
    } else if (sgv <= 4) {
      LOW.push(sgv);
    } else if (sgv < 10 && sgv > 4) {
      NORM.push(sgv);
    }
  });

  let hight = ((HIGHT.length * 100) / data.length).toFixed(1); // > 10 mmol/l
  let low = ((LOW.length * 100) / data.length).toFixed(1); // < 4 mmol/l
  let norm = ((NORM.length * 100) / data.length).toFixed(1); // < 10 >4 mmol/l
  return { low, hight, norm, average };
}
