import { ref } from "vue";
const LOCALE_OPTIONS = {
  weekday: "short",
  year: "numeric",
  month: "short",
  day: "numeric",
};
export function genAllEntries(allData) {
  let newAllEntries = ref([]);
  let currentDay = ref();
  let currentHour = ref();
  for (let i = 0; i < allData.length; i++) {
    let Day = new Date(parseInt(allData[i].date)).toLocaleDateString("ru-RU", {
      weekday: "short",
    });
    let longDate = new Date(parseInt(allData[i].date)).toLocaleDateString(
      "ru-RU",
      {
        year: "numeric",
        month: "numeric",
        day: "numeric",
      }
    );
    if (Day !== currentDay.value) {
      newAllEntries.value.push({ day: Day, date: longDate, data: [] });
    } else {
      let hour = new Date(parseInt(allData[i].date)).toLocaleString("ru-RU", {
        hour: "numeric",
      });
      if (hour !== currentHour.value) {
        newAllEntries.value[newAllEntries.value.length - 1].data.push({
          time: hour,
          date: allData[i].date,
          _id: allData[i]._id,
          sgv: (allData[i].sgv / 18.02).toFixed(1),
          __typename: allData[i].__typename,
        });
      }
    }
    currentHour.value = new Date(
      parseInt(allData[i].date)
    ).toLocaleString("ru-RU", { hour: "numeric" });
    currentDay.value = new Date(
      parseInt(allData[i].date)
    ).toLocaleDateString("ru-RU", { weekday: "short" });
  }
  return { newAllEntries };
}

export function genWeekEntries(allData) {
  let newEntries = ref([]);
  let currentDay = ref();
  const allWeekData = allData.filter(
    (el) => parseInt(el.date) > parseInt(allData[0].date) - 604800000
  );

  const startPeriod = ref(
    new Date(parseInt(allData[0].date)).toLocaleDateString(
      "ru-RU",
      LOCALE_OPTIONS
    )
  );
  const endPeriod = ref(
    new Date(
      parseInt(allWeekData[allWeekData.length - 1].date)
    ).toLocaleDateString("ru-RU", LOCALE_OPTIONS)
  );
  for (let i = 0; i < allWeekData.length; i++) {
    let Day = new Date(
      parseInt(allWeekData[i].date)
    ).toLocaleDateString("ru-RU", { weekday: "short" });
    if (Day !== currentDay.value) {
      newEntries.value.push({ day: Day, data: [] });
    } else {
      newEntries.value[newEntries.value.length - 1].data.push({
        date: allWeekData[i].date,
        _id: allWeekData[i]._id,
        sgv: (allWeekData[i].sgv / 18.02).toFixed(1),
        __typename: allWeekData[i].__typename,
      });
    }
    currentDay.value = new Date(
      parseInt(allWeekData[i].date)
    ).toLocaleDateString("ru-RU", { weekday: "short" });
  }

  return { startPeriod, newEntries, endPeriod };
}
