<template>
  <span
    class="
      flex
      items-center
      flex-wrap
      border
      rounded-md
      justify-between
      my-2
      text-glucoseDataFont
    "
  >
    <p class="pl-1">
      {{ timeLine }}
    </p>
    <p
      :class="[
        Entrie.sgv > 10 ? 'bg-hightGlucose bg-opacity-50 text-white' : '',
        Entrie.sgv < 4 ? 'bg-lowGlucose bg-opacity-50 text-white' : '',
      ]"
      class="pr-1"
    >
      {{ Entrie.sgv }}
    </p>
  </span>
</template>

<script>
import { ref, computed } from "vue";

export default {
  name: `DiaryItemData`,
  props: {
    Entrie: Object,
  },
  setup(props) {
    let timeLine = ref(
      new Date(parseInt(props.Entrie.date)).toLocaleString("ru-RU", {
        hour: "numeric",
        minute: "numeric",
      })
    );
    return {
      timeLine,
    };
  },
};
</script>

