import gql from "graphql-tag";

export const firstDateQuery = gql`
  query getFirstDate {
    firstDate {
      date
    }
  }
`;
export const weekDataQuery = gql`
  query getWeekAllData($START_DATE: String!) {
    entries(START_DATE: $START_DATE) {
      date
      _id
      sgv
    }
  }
`;

export const allDataQuery = gql`
  query getAllDataQuery {
    allData {
      date
      _id
      sgv
    }
  }
`;